<template>
  <v-container class="d-flex flex-column px-0">
    <v-app-bar fixed class="top" app  color="white">
      <v-btn fab text small @click="$router.back(-1)" class="mt-2">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-2">历史发帖</span>
    </v-app-bar>

    <div class="mescroll_container_historyPosts" ref="viewBox" v-touch:swiperight="rightSlide">
      <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp">
        <div id="noMoreDataWrap"></div>
        <v-card
          color="white"
          width="100%"
          class="mt-2"
          flat
          rounded
          v-for="(item,i) in historyPosts"
          :key="i"
          @click="$router.push({
          path:'/postDetail',
          query:{id:item.id}
        });"
        >
          <v-card-text class="d-flex flex-column pt-2 pb-4">
            <div class="d-flex">
              <v-avatar size="40" class="ml-3 mb-1">
                <img :src="item.avatar" alt="头像" />
              </v-avatar>
              <div class="d-flex flex-column">
                <span class="text_small-- text_thick ml-2">#{{item.name}}</span>
                <span class="text_small--- ml-2 grey--text mt-n1">{{timeFormat(item.created_time)}}</span>
              </div>
            </div>
            <span class="text_small-- black--text">
              <span class="text_thick">【帖子】 {{item.title}}</span>
            </span>
            <div class="grey_background_historyPosts pa-1">
              <span class="text_thick">【内容】:</span>
              <span class="black--text">{{item.content}}</span>
            </div>
          </v-card-text>
        </v-card>
      </mescroll-vue>
    </div>
    <v-snackbar v-model="snackbar.show" bottom :color="snackbar.color">{{snackbar.message}}</v-snackbar>
  </v-container>
</template>

<script>
import map from "@/map/map.js";
import MescrollVue from "mescroll.js/mescroll.vue"; 
import touch from '../../utils/touch'
export default {
  name: "historyPosts",
  components: {
    MescrollVue, // 注册mescroll组件
  },
  data() {
    return {
      historyPosts: [],
      snackbar: {
        show: false,
        message: "",
        color: "grey-darken3",
      },
      map: map,
      mescrollDown: {
        callback: this.refreshData,
        use: true,
      },
      mescrollUp: {
        callback: this.getMoreData,
        noMoreSize: 3,
        empty: {
          warpId: 'noMoreDataWrap',
          icon: null,
          tip: "暂无历史发帖",
          btntext: "",
          btnClick: null,
          supportTap: false
        }
      },
    };
  },
  created() {
  },
  methods: {
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = ''
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date.toJSON().substr(0, 16).replace('T', ' ');
      }
      return res;
    },
    refreshData(mescroll) {
      let that = this;
      that.$request('get', '/forum/getHistoryPosts', {
        timeToken: new Date().getTime()
      }, {})
        .then((res) => {
          that.historyPosts = res.data;
          mescroll.endSuccess(res.data.length);
        })
        .catch((e) => {
          mescroll.endErr();
        });
    },

    getMoreData(page, mescroll) {
      let that = this;
      let time = that.historyPosts.length > 0 ?
        new Date(that.historyPosts[that.historyPosts.length - 1].created_time).getTime()
        : new Date().getTime();
      that.$request('get', '/forum/getHistoryPosts', {
        timeToken: time
      })
        .then((res) => {
          if (page.num === 1) that.historyPosts = []
          console.log(res.data)
          that.historyPosts.push(...res.data);
          mescroll.endSuccess(res.data.length);
        })
        .catch((e) => {
          mescroll.endErr();
        });
    },
     rightSlide(){
      this.$router.go(-1)
    }
  },
  directives: {
    touch
  }
};
</script>

<style>
.main_container_historyPosts {
  margin-top: 50px;
}
.grey_background_historyPosts {
  background-color: #f5f5f5;
  border-radius: 10px;
  width: 90%;
}
.mescroll_container_historyPosts {
  position: fixed;
  top: 60px;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  width: 100%;
  background-color: #f5f5f5;
}
</style>



